import React, { memo } from 'react';
import { arrayOf, shape, string } from 'prop-types';

/**
 * @param {Object} Props
 * @returns {Node} React Component
 */
const SelectOptions = memo(
  ({ list }) =>
    list.map((item, idx) => (
      <option disabled={item.disabled} key={idx} value={item.value}>
        {item.value}
      </option>
    )),
  (prevProp, nextProp) => !(JSON.stringify(prevProp.list) !== JSON.stringify(nextProp.list))
);

SelectOptions.propTypes = {
  list: arrayOf(
    shape({
      disabled: string,
      value: string
    })
  )
};

SelectOptions.displayName = 'SelectOptions';

export default SelectOptions;
