import React, { memo } from 'react';
import { arrayOf, bool, func, string } from 'prop-types';
import Select from '../select';
import Options from './options';

/**
 * @param {object} Props
 * @returns {node} React Component
 */
const RenderComponent = memo(
  ({
    blurHandler,
    changeHandler,
    defaultSelection,
    errorText,
    id,
    invalid,
    label,
    options,
    optionType,
    selectedItem,
    selectedMonth,
    selectedYear
  }) => (
    <Select
      aria-label={label}
      defaultValue={selectedItem ? undefined : ''}
      errorText={errorText}
      id={id}
      invalid={invalid}
      onBlur={blurHandler}
      onChange={changeHandler}
      showErrorText
      value={selectedItem}
    >
      <Options
        defaultSelection={defaultSelection}
        list={options}
        listType={optionType}
        selectedMonth={selectedMonth ? selectedMonth : undefined}
        selectedYear={selectedYear ? selectedYear : undefined}
      />
    </Select>
  )
);

RenderComponent.propTypes = {
  blurHandler: func,
  changeHandler: func,
  defaultSelection: string,
  errorText: string,
  id: string,
  invalid: bool,
  label: string,
  optionType: string,
  options: arrayOf(string),
  selectedItem: string,
  selectedMonth: string,
  selectedYear: string
};

RenderComponent.displayName = 'RenderComponent';

export default RenderComponent;
