import React from 'react';
import { bool, func, string } from 'prop-types';
import RenderComponent from './render-component';
import { getMonths } from '@visa/ui-utils/lib/js/card-expiration';

const months = getMonths();

/**
 * @param {object} Props
 * @returns {node} React Component
 */
const ExpirationMonth = ({
  blurMonthField,
  errorText,
  id,
  invalid,
  labelForSelectMonth,
  placeholderForMonth,
  selectMonth,
  selectedMonth,
  selectedYear
}) => (
  <RenderComponent
    blurHandler={blurMonthField}
    changeHandler={selectMonth}
    defaultSelection={placeholderForMonth}
    errorText={errorText}
    id={id}
    invalid={invalid}
    label={labelForSelectMonth}
    options={months}
    optionType="MONTH"
    selectedItem={selectedMonth}
    selectedYear={selectedYear}
  />
);

ExpirationMonth.propTypes = {
  blurMonthField: func,
  errorText: string,
  id: string,
  invalid: bool,
  labelForSelectMonth: string,
  placeholderForMonth: string,
  selectMonth: func,
  selectedMonth: string,
  selectedYear: string
};

export default ExpirationMonth;
