/* eslint-disable no-console */

import React from 'react';
import ButtonText from '../../button-text';
import CardExpiration from '../../card-expiration';
import Col from '../../col';
import Grid from '../../grid';
import List from '../../list';
import Row from '../../row';
import Typography from '../../typography';

const Formik = require('formik').Formik;

const CardExpirationExample = () => (
  <Grid>
    <Row>
      <Col>
        <Typography tag="h2">Card Expiration - With default labels</Typography>
      </Col>
    </Row>
    <Row>
      <Col>
        <CardExpiration id="default-test" />
      </Col>
    </Row>
    <Row>
      <Col>
        <Typography tag="h2">Card Expiration</Typography>
        <List>
          <li>With labels (locale: en-US) from prop</li>
          <li>validateOnBlur</li>
          <li>Integrated with Formik (3rd Party Form)</li>
        </List>
      </Col>
    </Row>
    <Row>
      <Col>
        <Formik
          validate={values => {
            const errors = {};
            if (!values.month || !values.year) {
              errors.expiration = true;
            }
            // focus for invalid fields need to be managed by product teams.
            // below is a contrived example
            if (!values.year) {
              document.querySelector('#year-card-expiration-demo-1').focus();
            }
            if (!values.month) {
              document.querySelector('#month-card-expiration-demo-1').focus();
            }
            return errors;
          }}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2)); // eslint-disable-line
              setSubmitting(false);
            }, 400);
          }}
        >
          {({
            errors,
            handleSubmit,
            isSubmitting,
            submitCount,
            values
          }) => (
            <form onSubmit={handleSubmit}>
              <CardExpiration
                errorForEmptyFields="Please fill out the Expires field."
                errorForInvalidMonth="Please fill out the Expires Month."
                errorForInvalidYear="Please fill out the Expires Year."
                formSubmitCounter={errors.expiration ? submitCount : undefined}
                id="card-expiration-demo-1"
                labelForFieldset="Expires (MM / YY)"
                labelForSelectMonth="expires month"
                labelForSelectYear="expires year"
                onBlurMonthHandler={() => console.log('application:::on blur month handler')}
                onBlurYearHandler={() => console.log('application:::on blur year handler')}
                onChangeMonthHandler={e => {
                  console.log('application:::on change month handler');
                  values.month = e.target.value;
                }}
                onChangeYearHandler={e => {
                  console.log('application:::on change year handler');
                  values.year = e.target.value;
                }}
                placeholderForMonth="MM"
                placeholderForYear="YY"
                validateOnBlur
              />
              <ButtonText type="submit" disabled={isSubmitting}>
                Submit
              </ButtonText>
            </form>
          )}
        </Formik>
      </Col>
    </Row>
    <Row>
      <Col>
        <Typography tag="h2">Card Expiration</Typography>
        <List>
          <li>With labels (locale: fr-FR) from prop</li>
          <li>Integrated with Formik (3rd Party Form)</li>
        </List>
      </Col>
    </Row>
    <Row>
      <Col>
        <Formik
          validate={values => {
            const errors = {};
            if (!values.month || !values.year) {
              errors.expiration = true;
            }
            // focus for invalid fields need to be managed by product teams.
            // below is a contrived example
            if (!values.year) {
              document.querySelector('#year-card-expiration-demo-1').focus();
            }
            if (!values.month) {
              document.querySelector('#month-card-expiration-demo-1').focus();
            }
            return errors;
          }}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2)); // eslint-disable-line
              setSubmitting(false);
            }, 400);
          }}
        >
          {({
            errors,
            handleSubmit,
            isSubmitting,
            submitCount,
            values
          }) => (
            <form onSubmit={handleSubmit} lang="fr">
              <CardExpiration
                errorForEmptyFields="S'il vous plaît remplir le champ Expires."  // eslint-disable-line
                errorForInvalidMonth="S'il vous plaît remplir le mois expire." // eslint-disable-line
                errorForInvalidYear="S'il vous plaît remplir l'année expire." // eslint-disable-line
                formSubmitCounter={errors.expiration ? submitCount : undefined}
                id="card-expiration-demo-2"
                labelForFieldset="Expire (MM / AA)"
                labelForSelectMonth="expire mois" // eslint-disable-line
                labelForSelectYear="expire année" // eslint-disable-line
                onBlurMonthHandler={() => console.log('application:::on blur month handler')}
                onBlurYearHandler={() => console.log('application:::on blur year handler')}
                onChangeMonthHandler={e => {
                  console.log('application:::on change month handler');
                  values.month = e.target.value;
                }}
                onChangeYearHandler={e => {
                  console.log('application:::on change year handler');
                  values.year = e.target.value;
                }}
                placeholderForMonth="MM"
                placeholderForYear="AA"
              />
              <ButtonText type="submit" disabled={isSubmitting}>
                Soumettre
              </ButtonText>
            </form>
          )}
        </Formik>
      </Col>
    </Row>
  </Grid>
);

export default CardExpirationExample;
