import React, { memo, useEffect, useState } from 'react';
import classnames from 'classnames';
import { bool, func, node, number, string } from 'prop-types';
import ExpirationMonth from './expiration-month';
import ExpirationYear from './expiration-year';
import SelectGroup from '../select-group';
import SelectGroupDelimiter from '../select-group-delimiter';
import { isValidMonthYear } from '@visa/ui-utils/lib/js/card-expiration';

const CSS_PREFIX = 'vds-card-expiration';

/**
 * @param {object} Props
 * @returns {node} React Component
 */
const CardExpiration = memo(
  ({
    className,
    css,
    delimiter,
    errorForEmptyFields,
    errorForInvalidMonth,
    errorForInvalidYear,
    formSubmitCounter,
    id,
    labelForFieldset,
    labelForSelectMonth,
    labelForSelectYear,
    onBlurMonthHandler,
    onBlurYearHandler,
    onChangeMonthHandler,
    onChangeYearHandler,
    placeholderForMonth,
    placeholderForYear,
    selectedMonth,
    selectedYear,
    validateOnBlur
  }) => {
    const [emptyFields, setEmptyFields] = useState();
    const [month, setMonth] = useState({
      errorText: undefined,
      selected:
        selectedMonth && selectedYear
          ? isValidMonthYear(selectedMonth, selectedYear)
            ? selectedMonth
            : undefined
          : selectedMonth
    });
    const [year, setYear] = useState({
      errorText: undefined,
      selected:
        selectedMonth && selectedYear
          ? isValidMonthYear(selectedMonth, selectedYear)
            ? selectedYear
            : undefined
          : selectedYear
    });

    useEffect(() => {
      if (formSubmitCounter) {
        if (!month.selected && !year.selected) {
          setMonth({
            isError: ''
          });
          setYear({
            isError: ''
          });
          setEmptyFields(errorForEmptyFields);
        } else if (!month.selected && year.selected) {
          setMonth({
            errorText: errorForInvalidMonth
          });
        } else if (month.selected && !year.selected) {
          setYear({
            errorText: errorForInvalidYear
          });
        }
      }
    }, [errorForEmptyFields, errorForInvalidMonth, errorForInvalidYear, formSubmitCounter, month.selected, year.selected]);

    /**
     * @param {ReactSyntheticEvent} e
     */
    const changeMonth = e => {
      setEmptyFields(undefined);
      setMonth({
        errorText: undefined,
        selected: e.target.value
      });

      typeof onChangeMonthHandler === 'function' && onChangeMonthHandler(e);
    };

    /**
     * @param {ReactSyntheticEvent} e
     */
    const changeYear = e => {
      setYear({
        errorText: undefined,
        selected: e.target.value
      });

      if (emptyFields && !month.selected) {
        setEmptyFields(undefined);
        setMonth({
          errorText: errorForInvalidMonth
        });
      }

      setEmptyFields(undefined);

      typeof onChangeYearHandler === 'function' && onChangeYearHandler(e);
    };

    /**
     * @param {ReactSyntheticEvent} e
     */
    const blurMonthField = e => {
      if (validateOnBlur) {
        if (!e.target.value && !emptyFields) {
          setMonth({
            errorText: errorForInvalidMonth
          });
        }
      }

      typeof onBlurMonthHandler === 'function' && onBlurMonthHandler(e);
    };

    /**
     * @param {ReactSyntheticEvent} e
     */
    const blurYearField = e => {
      if (validateOnBlur) {
        if (!e.target.value && month.selected) {
          setYear({
            errorText: errorForInvalidYear
          });
        } else if (!e.target.value && !month.selected) {
          setMonth({
            errorText: undefined
          });
          setEmptyFields(errorForEmptyFields);
        }
      }

      typeof onBlurYearHandler === 'function' && onBlurYearHandler(e);
    };

    const invalidGroup = typeof emptyFields === 'string' ? Boolean(emptyFields) : undefined;

    return (
      <SelectGroup
        className={classnames(CSS_PREFIX, className)}
        css={css}
        errorText={emptyFields}
        id={id}
        invalid={invalidGroup}
        legend={labelForFieldset}
        showErrorText={Boolean(emptyFields)}
      >
        <ExpirationMonth
          blurMonthField={blurMonthField}
          errorText={month.errorText}
          id={`month-${id}`}
          invalid={invalidGroup || (typeof month.errorText === 'string' ? Boolean(month.errorText) : undefined)}
          labelForSelectMonth={labelForSelectMonth}
          placeholderForMonth={placeholderForMonth}
          selectMonth={changeMonth}
          selectedMonth={month.selected}
          selectedYear={year.selected}
        />
        <SelectGroupDelimiter>{delimiter}</SelectGroupDelimiter>
        <ExpirationYear
          blurYearField={blurYearField}
          errorText={year.errorText}
          id={`year-${id}`}
          invalid={invalidGroup || (typeof year.errorText === 'string' ? Boolean(year.errorText) : undefined)}
          labelForSelectYear={labelForSelectYear}
          placeholderForYear={placeholderForYear}
          selectYear={changeYear}
          selectedMonth={month.selected}
          selectedYear={year.selected}
        />
      </SelectGroup>
    );
  }
);

CardExpiration.propTypes = {
  /**
   * @ignore
   */
  className: string,
  /**
   * @ignore
   */
  css: func,
  /**
   *   Delimiter used in between month and year select.
   */
  delimiter: node,
  /**
   *   Error text used for empty fields.
   */
  errorForEmptyFields: string,
  /**
   *   Error text used for an invalid month.
   */
  errorForInvalidMonth: string,
  /**
   *   Error text used for an invalid year.
   */
  errorForInvalidYear: string,
  /**
   *   Number of times a form was submitted.
   */
  formSubmitCounter: number,
  /**
   *   Select group Id.
   */
  id: string,
  /**
   *   Label for select group.
   */
  labelForFieldset: string,
  /**
   *   Label for month select.
   */
  labelForSelectMonth: string,
  /**
   *   Label for year select.
   */
  labelForSelectYear: string,
  /**
   *   Callback to fire when you remove focus from a month select.
   */
  onBlurMonthHandler: func,
  /**
   *   Callback to fire when you remove focus from a year select.
   */
  onBlurYearHandler: func,
  /**
   *   Callback to fire when month value changes.
   */
  onChangeMonthHandler: func,
  /**
   *   Callback to fire when year value changes.
   */
  onChangeYearHandler: func,
  /**
   *   Placeholder label for month select.
   */
  placeholderForMonth: string,
  /**
   *   Placeholder label for year select.
   */
  placeholderForYear: string,
  /**
   *   Selected month when component first renders.
   */
  selectedMonth: string,
  /**
   *   Selected year when component first renders.
   */
  selectedYear: string,
  /**
   *   If true, will validate drop-downs on blur.
   */
  validateOnBlur: bool
};

CardExpiration.defaultProps = {
  delimiter: ' / ',
  errorForEmptyFields: 'Error for empty fields',
  errorForInvalidMonth: 'Error for invalid month',
  errorForInvalidYear: 'Error for invalid year',
  formSubmitCounter: 0,
  labelForFieldset: 'Label for fieldset',
  labelForSelectMonth: 'Label for Month',
  labelForSelectYear: 'Label for Year',
  placeholderForMonth: 'Placeholder text',
  placeholderForYear: 'Placeholder text',
  validateOnBlur: false
};

CardExpiration.displayName = 'CardExpiration';

export default CardExpiration;
