import React, { Fragment, memo } from 'react';
import { arrayOf, string } from 'prop-types';
import SelectOptions from './select-options';
import { isValidMonthYear } from '@visa/ui-utils/lib/js/card-expiration';

const isDisabled = (type, item, selectedMonth, selectedYear) => {
  let result;
  if (type === 'MONTH') {
    result = isValidMonthYear(item, selectedYear) ? undefined : 'disabled';
  } else if (type === 'YEAR') {
    result = isValidMonthYear(selectedMonth, item) ? undefined : 'disabled';
  }

  return result;
};

/**
 * @param {Object} Props
 * @returns {Node} React Component
 */
const Options = memo(({ defaultSelection, list, listType, selectedMonth, selectedYear }) => {
  const options = list.map(item => ({
    disabled: isDisabled(listType, item, selectedMonth, selectedYear),
    value: item
  }));
  return (
    <Fragment>
      <option value="" disabled>
        {defaultSelection}
      </option>
      <SelectOptions list={options} />
    </Fragment>
  );
});

Options.propTypes = {
  defaultSelection: string,
  list: arrayOf(string),
  listType: string,
  selectedMonth: string,
  selectedYear: string
};

Options.displayName = 'Options';

export default Options;
