import React from 'react';
import { bool, func, string } from 'prop-types';
import RenderComponent from './render-component';
import { getYears } from '@visa/ui-utils/lib/js/card-expiration';

const years = getYears();

/**
 * @param {object} Props
 * @returns {node} React Component
 */
const ExpirationYear = ({
  blurYearField,
  errorText,
  id,
  invalid,
  labelForSelectYear,
  placeholderForYear,
  selectYear,
  selectedMonth,
  selectedYear
}) => (
  <RenderComponent
    blurHandler={blurYearField}
    changeHandler={selectYear}
    defaultSelection={placeholderForYear}
    errorText={errorText}
    id={id}
    invalid={invalid}
    label={labelForSelectYear}
    options={years}
    optionType="YEAR"
    selectedItem={selectedYear}
    selectedMonth={selectedMonth}
  />
);

ExpirationYear.propTypes = {
  blurYearField: func,
  errorText: string,
  id: string,
  invalid: bool,
  labelForSelectYear: string,
  placeholderForYear: string,
  selectYear: func,
  selectedMonth: string,
  selectedYear: string
};

export default ExpirationYear;
